<template>
  <div class="videosame">
    <a v-for="(item,index) in skyVideos" @click="moveVideo(item,index)" :key="index">
      <img :src="item.img" />
      <div class="info">
        <p class="title">{{item.title}}</p>
        <p class="up">{{item.up}}</p>
        <p class="play">{{item.plays + '万播放'}}</p>
      </div>
    </a>
  </div>
</template>

<script>
import skyVue from "../components/video/sky.vue";
export default {
  props: ["skyVideos"],
  data() {
    return {
      videos: []
    };
  },
  methods: {
    moveVideo(item, index) {
      // console.log(index);
      this.$router.push({
        name: "video",
        params: {
          id: index,
          img: item.img,
          video: item.video
        }
      });
    }
  },
  mounted() {
    // console.log(this.skyVideos);
  }
};
</script>


<style lang="" scoped>
.videosame {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.videosame:after {
  display: block;
  content: "";
  width: 21%;
  height: 0px;
}
a {
  padding-top: 20px;
  cursor: pointer;
  position: relative;
}
a:hover .info {
  transition: 1s;
  bottom: 10px;
}
a:hover .title {
  overflow: none;
  text-overflow: none;
  white-space: pre-wrap;
}
a:hover .up,
a:hover .play {
  display: inline-block;
}
.info {
  color: #fff;
  box-sizing: border-box;
  width: 210px;
  position: absolute;
  bottom: 0;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
}
.title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.up,
.play {
  display: none;
}
img {
  width: 210px;
  height: 120px;
}
</style>



